@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
body{font-family:"Rubik", sans-serif;margin:0;background-color:#483d8b;color:#fff}button{border:none;font-size:16px;border-radius:5px;transition:0.3s ease-in-out;cursor:pointer}button:hover{background:green !important}h1,h2,h3,h4,h5,body{font-weight:400}

header{padding:16px 0;background-color:#483d8b;color:white;margin-bottom:32px}header ul{display:flex;flex-wrap:wrap;padding:0}header ul li{flex:0 0 48%;font-weight:600;list-style:none}header ul li a{cursor:pointer;color:#fff;text-decoration:underline}header ul li:nth-child(2){flex:0 0 47%}header ul li:last-child{flex:1 1;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content}@media screen and (max-width: 1200px) and (min-width: 1028px){header ul li{flex:0 0 47%}}@media screen and (max-width: 1028px) and (min-width: 768px){header ul li{flex:0 0 45%}}@media screen and (max-width: 480px) and (min-width: 320px){header{margin-bottom:8px}header ul li{flex:0 0 81%}header ul li:nth-child(2){flex:0 1}}

.wallet-modal{position:fixed;z-index:99;top:0;width:100%;text-align:center;height:100%;padding-top:128px;background:#8470f3;overflow:hidden;text-transform:uppercase}.wallet-modal h1{font-weight:800}.wallet-modal svg{position:relative;right:24px}.wallet-modal svg path{fill:#fff36b}@media screen and (max-width: 480px) and (min-width: 320px){.wallet-modal h1{font-size:28px}.wallet-modal svg{left:0;right:0}}

.smiley-market h2{font-size:40px;margin-top:0;margin-bottom:56px}.smiley-market-container{display:flex;flex-flow:wrap}.smiley-market-container-card{max-width:400px;margin-right:75px;margin-bottom:48px}.smiley-market-container-card:nth-child(3n+3){margin-right:0}.smiley-market-container-card img{object-fit:cover}.smiley-market-container-card-info button{font-weight:800;font-size:18px;background-color:#483d8b;border:1px solid;color:#fff;padding:4px 28px 4px 8px}.smiley-market-container-card-info-cost{display:flex;margin-bottom:24px}.smiley-market-container-card-info-cost svg{margin-left:-8px;margin-right:8px}.smiley-market-container-card-info-cost span{font-weight:800;margin-top:7px}@media screen and (max-width: 1200px) and (min-width: 1028px){.smiley-market h2{font-size:32px;margin-bottom:48px}.smiley-market-container-card{max-width:300px;margin-right:64px;margin-bottom:32px}.smiley-market-container-card img{max-width:300px;max-height:300px}}@media screen and (max-width: 1028px) and (min-width: 768px){.smiley-market h2{font-size:28px;margin-bottom:32px}.smiley-market h3{font-size:18px}.smiley-market-container-card{max-width:240px;margin-right:24px;margin-bottom:24px}.smiley-market-container-card img{max-width:240px;max-height:240px}.smiley-market-container-card-info button{font-size:16px}.smiley-market-container-card-info-cost{margin-bottom:16px}.smiley-market-container-card-info-cost span{font-size:16px}}@media screen and (max-width: 480px) and (min-width: 320px){.smiley-market h2{font-size:28px;margin-bottom:32px}.smiley-market h3{font-size:18px}.smiley-market-container-card{max-width:300px;margin-right:0;margin-bottom:24px}.smiley-market-container-card img{max-width:300px;max-height:300px}.smiley-market-container-card-info button{font-size:16px}.smiley-market-container-card-info-cost{margin-bottom:16px}.smiley-market-container-card-info-cost span{font-size:16px}}

.container{max-width:1200px;margin:0 auto}@media screen and (max-width: 1200px) and (min-width: 1028px){.container{max-width:1028px}}@media screen and (max-width: 1028px) and (min-width: 768px){.container{max-width:768px}}@media screen and (max-width: 480px) and (min-width: 320px){.container{max-width:320px}}

